import { Component, OnInit, ViewEncapsulation } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ReportService } from "../../services/report.service";
import { Router } from "@angular/router";
import { karigarSummaryReport } from "app/shared/reports/karigarSummary.service";
import { KarigarService } from "app/modules/karigar/service/karigar.service";

@Component({
  selector: "app-karigar-summary",
  templateUrl: "./karigar-summary.component.html",
  styleUrls: ["./karigar-summary.component.scss"],
  encapsulation: ViewEncapsulation.None,
})
export class karigarSummaryComponent implements OnInit {
  contentHeader: object;
  customerLedgerForm: FormGroup;
  tableDataArray = [];
  karigarDetail;
  customerData = {
    karigarName: null,
    karigarNumber: null,
    designation: null,
    address: null,
  };
  constructor(
    private _karigarService: KarigarService,
    private fb: FormBuilder,
    private reportService: ReportService,
    private _router: Router,
    private _karigarInvoice: karigarSummaryReport
  ) {}

  makeReactiveForm() {
    this.customerLedgerForm = this.fb.group({
      karigarName: ["", [Validators.required]],
      karigarNumber: [""],
      fromDate: [],
      toDate: [],
    });
  }

  onSubmit(formValue) {
    if (this.customerLedgerForm.invalid) {
      return;
    }
    this.reportService.karigarSummaryReport(formValue).subscribe((res) => {
      if (res.data) {
        this.tableDataArray = res.data;
      } else if (!res.data) {
        this.tableDataArray = [];
      }
    });
  }

  printReport() {
    const passAPi = {
      karigarName: this.customerData.karigarName,
      karigarNumber: this.customerData.karigarNumber,
      designation: this.customerData.designation,
      address: this.customerData.address,
      toDate: this.customerLedgerForm.controls.toDate.value,
      fromDate: this.customerLedgerForm.controls.fromDate.value,
      data: this.tableDataArray,
    };

    this._karigarInvoice.generatePdfContent(passAPi);
    this._router.navigate(["/reports/view"]);
  }

  getkarigarDetails() {
    this._karigarService.getAll().subscribe((res) => {
      if (res.data) {
        
        this.karigarDetail = res.data
        this.karigarDetail.unshift({
          name: 'All',
          phone: ''
        });;
      }
    });
  }

  getTotalAmount(): number {
    return this.tableDataArray.reduce((total, item) => total + +item.amount, 0);
  }


  selectCustomer(selectedCustomer) {
    
    this.customerData.karigarName = selectedCustomer.name;
    this.customerData.karigarNumber = selectedCustomer.phone;
    this.customerData.designation = selectedCustomer.designation;
    this.customerData.address = selectedCustomer.address;
  }
  ngOnInit(): void {
    this.makeReactiveForm();
    this.getkarigarDetails();
    this.contentHeader = {
      headerTitle: "Reports",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: "karigar Summary",
            isLink: true,
            link: "/",
          },
          {
            name: "Report",
            isLink: false,
          },
        ],
      },
    };
  }
}
