import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ListReportComponent } from './components/list-report/list-report.component';
import { ContentHeaderModule } from 'app/layout/components/content-header/content-header.module';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { CoreCommonModule } from '@core/common.module';
import { ViewReportComponent } from './components/view-report/view-report.component';
import { CustomerLedgerComponent } from './components/customer-ledger/customer-ledger.component';
import { NgSelectModule } from '@ng-select/ng-select';
import { ExpensesDetailComponent } from './components/expenses-deatail/expense-detail.component';
import { ExpensesSummaryComponent } from './components/expenses-summary/expense-summary.component';
import { OrderSummaryComponent } from './components/order-summary/order-summary.component';
import { InvoiceSummaryComponent } from './components/invoice-summary/invoice-summary.component';
import { karigarSummaryComponent } from './components/karigar-summary/karigar-summary.component';
import { InventoryActivityComponent } from './components/inventory-activity/inventory-activity.component';
import { karigarLedgerComponent } from './components/karigar-ledger/karigar-ledger.component';
import { WholesalePurchaseComponent } from './components/wholesale-purchase/wholesale-purchase.component';
import { WholesaleSalesComponent } from './components/wholesale-sale/wholesale-sale.component';
import { StichingOrderDetailComponent } from './components/stiching-order-detail/stiching-order-detail.component';

const routes: Routes = [
  {
    path: "",
    component: ListReportComponent,
    data: { animation: "report-list" },
  },
  {
    path: "customer-ledger",
    component: CustomerLedgerComponent,
    data: { animation: "report-list" },
  },
  {
    path: "expense-detail",
    component: ExpensesDetailComponent,
    data: { animation: "expense-detail" },
  },
  {
    path: "expense-summary",
    component: ExpensesSummaryComponent,
    data: { animation: "expense-summary" },
  },
  {
    path: "order-summary",
    component: OrderSummaryComponent,
    data: { animation: "order-summary" },
  },
  {
    path: "invoice-summary",
    component: InvoiceSummaryComponent,
    data: { animation: "invoice-summary" },
  },
  {
    path: "karigar-summary",
    component: karigarSummaryComponent,
    data: { animation: "karigar-summary" },
  },
  {
    path: "karigar-ledger",
    component: karigarLedgerComponent,
    data: { animation: "karigar-ledger" },
  },
  {
    path: "wholesale-purchase",
    component: WholesalePurchaseComponent,
    data: { animation: "wholesale-purchase" },
  },
  {
    path: "wholesale-sale",
    component: WholesaleSalesComponent,
    data: { animation: "wholesale-sale" },
  },
  {
    path: "stiching-customer",
    component: StichingOrderDetailComponent,
    data: { animation: "stiching-customer" },
  },
  {
    path: "inventory-activity",
    component: InventoryActivityComponent,
    data: { animation: "karigar-summary" },
  },
  {
    path: "view",
    component: ViewReportComponent,
    data: { animation: "report-view" },
  },
]

@NgModule({
  declarations: [
    ListReportComponent,
    ViewReportComponent,
    CustomerLedgerComponent,
    ExpensesDetailComponent,
    ExpensesSummaryComponent,
    OrderSummaryComponent,
    InvoiceSummaryComponent,
    karigarSummaryComponent,
    InventoryActivityComponent,
    karigarLedgerComponent,
    WholesalePurchaseComponent,
    WholesaleSalesComponent,
    StichingOrderDetailComponent
  ],
  imports: [
    CommonModule,
    ContentHeaderModule,
    RouterModule.forChild(routes),
    FormsModule,
    CoreCommonModule,
    NgSelectModule
  ]
})
export class ReportModule { }
