import { Injectable } from "@angular/core";
import { BehaviorSubject, Subject } from "rxjs";
import { numberToWords } from "../number-to-words";
import { reportViewInHtmlReport } from "./reportViewInHtml.service";
import {
  dateFormatter,
  thousandSeparator,
  handleNullUndefinedValues,
} from "../date-format";
@Injectable({
  providedIn: "root",
})
export class inventoryFabricReport {
  constructor(private _reportView: reportViewInHtmlReport) {}

  async generatePdfContent(getDetail) {
    getDetail =await handleNullUndefinedValues(getDetail);
    const makePdf = `
    <div class="card invoice-preview-card">
    <div class="divider">
      <div class="divider-text"><h3>INVENTORY REPORT (FABRIC)</h3></div>
    </div>
    <div class="table-responsive">
      <table class="table">
        <colgroup>
          <col width="20%" />
          <col width="20%" />
          <col width="20%" />
          <col width="10%" />
          <col width="15%" />
          <col width="15%" />
        </colgroup>
        <thead>
          <tr>
            <th class="py-1">Item</th>
            <th class="py-1">Stich Type</th>
            <th class="py-1">Article</th>
            <th class="py-1">Color</th>
            <th class="py-1">UOM</th>
            <th class="py-1">Quantity</th>
          </tr>
        </thead>
        <tbody>
          ${getDetail.map((element) => {
       
            return `
          <tr class="border-bottom">
            <td class="py-1">${element.name}</td>
            <td class="py-1">${element.itemType}</td>
            <td class="py-1">${element.article}</td>
            <td class="py-1">${element.color}</td>
            <td class="py-1">${element.UOM}</td>
            <td class="py-1">${element.closingQuantity}</td>
          </tr>
          `;
          })}
        </tbody>
      </table>
    </div>
    <div class="card-body invoice-padding pt-0">
      <div class="row">
        <div class="col-12" style="height: 100px">
          <span class="font-weight-bold"></span>
          <span>.</span>
        </div>
      </div>
    </div>
  </div>`;
    this._reportView.getPdfContent.next(makePdf);
  }
}
