import { Component, OnInit, ViewEncapsulation } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { UserService } from "app/modules/user/service/user.service";
import { ReportService } from "../../services/report.service";
import { Router } from "@angular/router";
import { expensesSummaryReport } from "app/shared/reports/expensesSummary.service";

@Component({
  selector: "app-expense-summary",
  templateUrl: "./expense-summary.component.html",
  styleUrls: ["./expense-summary.component.scss"],
  encapsulation: ViewEncapsulation.None,
})
export class ExpensesSummaryComponent implements OnInit {
  contentHeader: object;
  customerLedgerForm: FormGroup;
  tableDataArray = [];
  public businessType = ["Needlework Stiching", "Needlework Fabric Wholesale"];
  
  constructor(
    private fb: FormBuilder,
    private reportService: ReportService,
    private _router: Router,
    private _expensesSummary: expensesSummaryReport
  ) {}

  makeReactiveForm() {
    this.customerLedgerForm = this.fb.group({
      businessType: [null, [Validators.required]],
      fromDate: [null, [Validators.required]],
      toDate: [null, [Validators.required]],
    });
  }

  onSubmit(formValue) {
    if (this.customerLedgerForm.invalid) {
      return;
    }
    this.reportService.expensesSummaryReport(formValue).subscribe((res) => {
      if (res.data) {
        this.tableDataArray = res.data;
      } else if (!res.data) {
        this.tableDataArray = [];
      }
    });
  }

  printReport() {
    const passAPi = {
      businessType: this.customerLedgerForm.value.businessType,
      toDate: this.customerLedgerForm.value.toDate,
      fromDate: this.customerLedgerForm.value.fromDate,
      data: this.tableDataArray
    };
  
    this._expensesSummary.generatePdfContent(passAPi);
    this._router.navigate(["/reports/view"]);
  }

  getTotalAmount(): number {
    return this.tableDataArray.reduce((total, item) => total + +item.amount, 0);
  }

  ngOnInit(): void {
    this.makeReactiveForm();
    this.contentHeader = {
      headerTitle: "Reports",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: "Expenses Summary",
            isLink: true,
            link: "/",
          },
          {
            name: "Report",
            isLink: false,
          },
        ],
      },
    };
  }
}
