import { Injectable } from "@angular/core";
import { BehaviorSubject, Subject } from "rxjs";
import { reportViewInHtmlReport } from "./reportViewInHtml.service";
import {
  dateFormatter,
  thousandSeparator,
  handleNullUndefinedValues,
} from "../date-format";
@Injectable({
  providedIn: "root",
})
export class StichingCustomerOrderReport {
  constructor(private _reportView: reportViewInHtmlReport) {}

 async generatePdfContent(getDetail) {
    getDetail =await handleNullUndefinedValues(getDetail);
    let totalAmount = 0;
    let totalReceived = 0;
    let totalBalance = 0;
    const makePdf = `
    <div class="card invoice-preview-card">
    <div class="divider">
      <div class="divider-text"><h3>Stiching Customer Orders Detail</h3></div>
    </div>
    <div class="d-flex justify-content-between">
      <div class="ml-2">
        <div class="invoice-date-wrapper mb-0">
          <p class="invoice-date">From Date:</p>
          <p class="invoice-date-title ml-25">${dateFormatter(
            getDetail.fromDate
          )}</p>
        </div>
        <div class="invoice-date-wrapper">
          <p class="invoice-date">To Date:</p>
          <p class="invoice-date-title ml-25">${dateFormatter(
            getDetail.toDate
          )}</p>
        </div>
      </div>
    </div>
    <hr class="m-0" />
    <div class="table-responsive">
      <table class="table">
        <colgroup>
          <col width="15%" />
          <col width="15%" />
          <col width="25%" />
          <col width="15%" />
          <col width="10%" />
          <col width="10%" />
          <col width="10%" />
        </colgroup>
        <thead>
          <tr>
            <th class="py-1">Date</th>
            <th class="py-1">Order No</th>  
            <th class="py-1">Customer Name</th>
            <th class="py-1">Item Name</th>
            <th class="py-1 text-right">Net Amount</th>
            <th class="py-1 text-right">Amount Received</th>
            <th class="py-1 text-right">Balance Amount</th>
          </tr>
        </thead>
        <tbody>
          ${getDetail.data.map((element) => {
            totalAmount += +element.total_amount;
            totalReceived += +element.amount_received;
            totalBalance += +element.balance_amount;
            return `<tr class="border-bottom">
                      <td class="py-1">${dateFormatter(element.created_at)}</td>
                      <td class="py-1">WN-00${element.id}</td>
                      <td class="py-1">${element.customer_name}</td>
                      <td class="py-1">${element.item_master}</td>
                      <td class="py-1 text-right">${thousandSeparator(element.total_amount)}</td>
                      <td class="py-1 text-right">${thousandSeparator(element.amount_received)}</td>
                      <td class="py-1 text-right">${thousandSeparator(element.balance_amount)}</td>
                    </tr>`;
          })}
          <tr class="border-bottom border-top bg-light">
            <td colspan="4">
              <span class="font-weight-bold">Total</span>
            </td>
            <td class="text-right"><span class="font-weight-bold">${thousandSeparator(totalAmount)}</span></td>
            <td class="text-right"><span class="font-weight-bold">${thousandSeparator(totalReceived)}</span></td>
            <td class="text-right"><span class="font-weight-bold">${thousandSeparator(totalBalance)}</span></td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="card-body invoice-padding pt-0">
      <div class="row">
        <div class="col-12" style="height: 100px">
          <span class="font-weight-bold">. </span>
        </div>
      </div>
    </div>
  </div>`;
    this._reportView.getPdfContent.next(makePdf);
  }
}
