import { Component, OnInit, ViewEncapsulation } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { UserService } from "app/modules/user/service/user.service";
import { ReportService } from "../../services/report.service";
import { Router } from "@angular/router";
import { invoiceSummaryReport } from "app/shared/reports/invoiceSummary.service";

@Component({
  selector: "app-invoice-summary",
  templateUrl: "./invoice-summary.component.html",
  styleUrls: ["./invoice-summary.component.scss"],
  encapsulation: ViewEncapsulation.None,
})
export class InvoiceSummaryComponent implements OnInit {
  contentHeader: object;
  customerLedgerForm: FormGroup;
  tableDataArray = [];
  userDetail;
  customerData = {
    customerName: null,
    customerNumber: null,
    email: null,
    address: null,
    city: null,
  };
  constructor(
    private _customerService: UserService,
    private fb: FormBuilder,
    private reportService: ReportService,
    private _router: Router,
    private _customerLedger: invoiceSummaryReport
  ) {}

  makeReactiveForm() {
    this.customerLedgerForm = this.fb.group({
      customerName: ["", [Validators.required]],
      customerMobile: [""],
      fromDate: [],
      toDate: [],
    });
  }

  onSubmit(formValue) {
    if (this.customerLedgerForm.invalid) {
      return;
    }
    this.reportService.invoiceSummaryReport(formValue).subscribe((res) => {
      if (res.data) {
        this.tableDataArray = res.data;
      } else if (!res.data) {
        this.tableDataArray = [];
      }
    });
  }

  printReport() {
    const passAPi = {
      customer_name: this.customerData.customerName,
      customer_phone: this.customerData.customerNumber,
      email: this.customerData.email,
      address: this.customerData.address,
      city: this.customerData.city,
      toDate: this.customerLedgerForm.controls.toDate.value,
      fromDate: this.customerLedgerForm.controls.fromDate.value,
      data: this.tableDataArray,
    };

    this._customerLedger.generatePdfContent(passAPi);
    this._router.navigate(["/reports/view"]);
  }

  getTotalNetAmount(): number {
    return this.tableDataArray.reduce((total, item) => total + +item.totalNetAmount, 0);
  }

  getAmountReceived(): number {
    return this.tableDataArray.reduce((total, item) => total + +item.amountReceived, 0);
  }

  getNetBalanceAmount(): number {
    return this.tableDataArray.reduce((total, item) => total + +item.netBalanceAmount, 0);
  }
  getUserDetails() {
    this._customerService.getAll().subscribe((res) => {
      if (res.data) {
        this.userDetail = res.data.filter(
          (type) => type.customerType == "Wholesale Fabric Supplier"
        )
        this.userDetail.unshift({
          full_name: 'All',
          phone: ''
        });
      }
    });
  }

  selectCustomer(selectedCustomer) {
    this.customerData.customerName = selectedCustomer.full_name;
    this.customerData.customerNumber = selectedCustomer.phone;
    this.customerData.email = selectedCustomer.email;
    this.customerData.address = selectedCustomer.address;
    this.customerData.city = selectedCustomer.city;
  }
  ngOnInit(): void {
    this.makeReactiveForm();
    this.getUserDetails();
    this.contentHeader = {
      headerTitle: "Reports",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: "Invoice Summary",
            isLink: true,
            link: "/",
          },
          {
            name: "Report",
            isLink: false,
          },
        ],
      },
    };
  }
}
