import { Injectable } from "@angular/core";
import { BehaviorSubject, Subject } from "rxjs";
import { reportViewInHtmlReport } from "./reportViewInHtml.service";
import {
  dateFormatter,
  thousandSeparator,
  handleNullUndefinedValues,
} from "../date-format";
@Injectable({
  providedIn: "root",
})
export class inventoryActivityReport {
  constructor(private _reportView: reportViewInHtmlReport) {}

  async generatePdfContent(getDetail) {
    getDetail =await handleNullUndefinedValues(getDetail);

    let totalCredit = 0;
    let totalDebit = 0;
    let totalBalance = 0;
    const makePdf = `
    <div class="card invoice-preview-card">
  <div class="divider">
  <div class="divider-text"><h3>INVENTORY ACTIVITY REPORT (FABRIC) </h3></div>
</div>
    <div class="card-body invoice-padding py-1">
      <div class="d-flex justify-content-between">
        <div>
          <div class="invoice-date-wrapper mb-0">
              <p class="invoice-date">From Date:</p>
              <p class="invoice-date-title ml-25">${dateFormatter(
                getDetail.fromDate
              )}</p>
          </div>
          <div class="invoice-date-wrapper">
            <p class="invoice-date">To Date:</p>
            <p class="invoice-date-title ml-25">${dateFormatter(
              getDetail.toDate
            )}</p>
          </div>
        </div>
      </div>
    </div>
    <hr class="m-0" />
    <div class="card-body invoice-padding pt-0">
      <div class="row invoice-spacing">
        <div class="col-6 p-0">
          <table>
            <tbody>
              <tr>
                <td class="pr-1">Item Name:</td>
                <td>${getDetail.itemName}</td>
              </tr>
              <tr>
                <td class="pr-1">Unit Of Measure (UOM):</td>
                <td>${getDetail.itemUOM}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="col-6 p-0">
          <div class="float-right">
            <table>
              <tbody>
                <tr>
                  <td class="pr-1">Article:</td>
                  <td>${getDetail.article}</td>
                </tr>
                <tr>
                  <td class="pr-1">Color:</td>
                  <td>${getDetail.color}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
    <div class="table-responsive">
      <table class="table">
        <colgroup>
          <col width="13%" />
          <col width="13%" />
          <col width="13%" />
          <col width="29%" />
          <col width="8%" />
          <col width="8%" />
          <col width="8%" />
          <col width="8%" />
        </colgroup>
        <thead>
          <tr>
            <th class="py-1">Date</th>
            <th class="py-1">Doc Number</th>
            <th class="py-1">Doc Type</th>
            <th class="py-1">Description</th>
            <th class="py-1">Opening</th>
            <th class="py-1">Stock In</th>
            <th class="py-1">Stock Out</th>
            <th class="py-1">Balance</th>
          </tr>
        </thead>
        <tbody>
          ${getDetail.data.map((element) => {
       
            totalCredit += +element.StockIn;
            totalDebit += +element.StockOut;
            return `
          <tr class="border-bottom">
            <td class="py-1">${dateFormatter(element.Date)}</td>
            <td class="py-1">${element.DocNumber}</td>
            <td class="py-1">${element.DocType}</td>
            <td class="py-1">${element.Description}</td>
            <td class="py-1">${element.Opening}</td>
            <td class="py-1 text-right ">${thousandSeparator(element.StockIn)}</td>
            <td class="py-1 text-right">${thousandSeparator(element.StockOut)}</td>
            <td class="py-1 text-right">${thousandSeparator(element.Balance)}</td>
          </tr>
          `;
          })}
          <tr class="border-bottom border-top bg-light">
          <td colspan="5"><span class="font-weight-bold">Closing Balance</span></td>
          <td class="text-right"><span class="font-weight-bold">${thousandSeparator(totalCredit)}</span></td>
          <td class="text-right"><span class="font-weight-bold">${thousandSeparator(totalDebit)}</span></td>
          <td class="text-right"><span class="font-weight-bold">${thousandSeparator(totalCredit-totalDebit)}</span></td>
        </tr>
        </tbody>
      </table>
    </div>
    <div class="card-body invoice-padding pt-0">
      <div class="row">
        <div class="col-12" style="height: 100px">
          <span class="font-weight-bold">. </span>
        </div>
      </div>
    </div>
  </div>`;
    this._reportView.getPdfContent.next(makePdf);
  }
}
