import { Component, OnInit, ViewEncapsulation } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ReportService } from "../../services/report.service";
import { Router } from "@angular/router";
import { expensesDetailsReport } from "app/shared/reports/expensesDetails.service";

@Component({
  selector: "app-expense-detail",
  templateUrl: "./expense-detail.component.html",
  styleUrls: ["./expense-detail.component.scss"],
  encapsulation: ViewEncapsulation.None,
})
export class ExpensesDetailComponent implements OnInit {
  contentHeader: object;
  expenseDetailForm: FormGroup;
  tableDataArray = [];
  public businessType = ["Needlework Stiching", "Needlework Fabric Wholesale"];
  public expensesType = [
    "Karigar Salary",
    "Utility Expenses",
    "Conveyance Expenses",
    "Tea Expenses",
    "Meal Expenses",
    "Guest Meal Expenses",
    "Office Expenses",
    "Miscellaneous Expenses",
  ];
  constructor(
    private fb: FormBuilder,
    private reportService: ReportService,
    private _router: Router,
    private _expenseDetail: expensesDetailsReport
  ) {}

  makeReactiveForm() {
    this.expenseDetailForm = this.fb.group({
      businessType: [null, [Validators.required]],
      expensesType: [null, [Validators.required]],
      fromDate: [],
      toDate: [],
    });
  }

  onSubmit(formValue) {
    if (this.expenseDetailForm.invalid) {
      return;
    }
    this.reportService.expensesDetailReport(formValue).subscribe((res) => {
      if (res.data) {
        this.tableDataArray = res.data;
      } else if (!res.data) {
        this.tableDataArray = [];
      }
    });
  }

  printReport() {
    const passAPi = {
      businessType: this.expenseDetailForm.value.businessType,
      expensesType: this.expenseDetailForm.value.expensesType,
      toDate: this.expenseDetailForm.value.toDate,
      fromDate: this.expenseDetailForm.value.fromDate,
      data: this.tableDataArray
    };
  
    this._expenseDetail.generatePdfContent(passAPi);
    this._router.navigate(["/reports/view"]);
  }

  getTotalAmount(): number {
    return this.tableDataArray.reduce((total, item) => total + item.amount, 0);
  }

  ngOnInit(): void {
    this.makeReactiveForm();
    this.contentHeader = {
      headerTitle: "Reports",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: "Expenses Detail",
            isLink: true,
            link: "/",
          },
          {
            name: "Report",
            isLink: false,
          },
        ],
      },
    };
  }
}
