import { Injectable } from "@angular/core";
import { BehaviorSubject, Subject } from "rxjs";
import { reportViewInHtmlReport } from "./reportViewInHtml.service";
import {
  dateFormatter,
  thousandSeparator,
  handleNullUndefinedValues,
} from "../date-format";
@Injectable({
  providedIn: "root",
})
export class WholesaleFabricPurchaseReport {
  constructor(private _reportView: reportViewInHtmlReport) { }

  async generatePdfContent(getDetail) {
    getDetail = await handleNullUndefinedValues(getDetail);
    let totalQty = 0;
    let totalNetRate = 0;
    let totalShipment = 0;
    let totalAmount = 0;
    let totalMisc = 0;
    let totalNetAmount = 0;
    const makePdf = `
    <div class="card invoice-preview-card">
    <div class="divider">
      <div class="divider-text"><h3>Wholesale Fabric Purchase</h3></div>
    </div>
    <div class="d-flex justify-content-between">
      <div class="ml-2">
        <div class="invoice-date-wrapper mb-0">
          <p class="invoice-date">From Date:</p>
          <p class="invoice-date-title ml-25">${dateFormatter(
      getDetail.fromDate
    )}</p>
        </div>
        <div class="invoice-date-wrapper">
          <p class="invoice-date">To Date:</p>
          <p class="invoice-date-title ml-25">${dateFormatter(
      getDetail.toDate
    )}</p>
        </div>
      </div>
    </div>
    <hr class="m-0" />
    <div class="table-responsive">
      <table class="table">
        <colgroup>
          <col width="15%" />
          <col width="15%" />
          <col width="25%" />
          <col width="15%" />
          <col width="10%" />
          <col width="10%" />
          <col width="10%" />
        </colgroup>
        <thead>
          <tr>
            <th class="p-1">Date</th>
            <th class="p-1">GRN No</th>  
            <th class="p-1">Supplier Name</th>
            <th class="p-1">Item Name</th>
            <th class="p-1">Colour</th>
            <th class="p-1">Article</th>
            <th class="p-1">UOM</th>
            <th class="p-1">QTY</th>
            <th class="p-1 text-right">Rate</th>
            <th class="p-1 text-right">Amount</th>
            <th class="p-1 text-right">Shipment Received</th>
            <th class="p-1 text-right">Misc Amount</th>
            <th class="p-1 text-right">Net Amount</th>
          </tr>
        </thead>
        <tbody>
          ${getDetail.data.map((element) => {
      totalQty += +element.itemQuantity;
      totalNetRate += +element.itemNetRate;
      totalAmount += +element.itemAmount;
      totalShipment += +element.itemShipment;
      totalMisc += +element.itemMiscCost;
      totalNetAmount += +element.itemNetAmount;
      return `<tr class="border-bottom">
                      <td class="p-1">${dateFormatter(element.created_at)}</td>
                      <td class="p-1">GN-00${element.masterId}</td>
                      <td class="p-1">${element.customerName}</td>
                      <td class="p-1">${element.itemMaster}</td>
                      <td class="p-1">${element.itemColor}</td>
                      <td class="p-1">${element.itemArticle}</td>
                      <td class="p-1">${element.itemUOM}</td>
                      <td class="p-1 text-right">${thousandSeparator(element.itemQuantity)}</td>
                      <td class="p-1 text-right">${thousandSeparator(element.itemNetRate)}</td>
                      <td class="p-1 text-right">${thousandSeparator(element.itemAmount)}</td>
                      <td class="p-1 text-right">${thousandSeparator(element.itemShipment)}</td>
                      <td class="p-1 text-right">${thousandSeparator(element.itemMiscCost)}</td>
                      <td class="p-1 text-right">${thousandSeparator(element.itemNetAmount)}</td>
                    </tr>`;
    })}
          <tr class="border-bottom border-top bg-light">
            <td colspan="7">
              <span class="font-weight-bold">Total</span>
            </td>
            <td class="text-right"><span class="font-weight-bold">${thousandSeparator(totalQty)}</span></td>
            <td class="text-right"><span class="font-weight-bold">${thousandSeparator(totalNetRate)}</span></td>
            <td class="text-right"><span class="font-weight-bold">${thousandSeparator(totalAmount)}</span></td>
            <td class="text-right"><span class="font-weight-bold">${thousandSeparator(totalShipment)}</span></td>
            <td class="text-right"><span class="font-weight-bold">${thousandSeparator(totalMisc)}</span></td>
            <td class="text-right"><span class="font-weight-bold">${thousandSeparator(totalNetAmount)}</span></td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="card-body invoice-padding pt-0">
      <div class="row">
        <div class="col-12" style="height: 100px">
          <span class="font-weight-bold">. </span>
        </div>
      </div>
    </div>
  </div>`;
    this._reportView.getPdfContent.next(makePdf);
  }
}
