import { Component, OnInit, ViewEncapsulation } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { UserService } from "app/modules/user/service/user.service";
import { ReportService } from "../../services/report.service";
import { Router } from "@angular/router";
import { StichingCustomerOrderReport } from "app/shared/reports/stichingCustomerOrder.service";

@Component({
  selector: "app-stiching-order-detail",
  templateUrl: "./stiching-order-detail.component.html",
  styleUrls: ["./stiching-order-detail.component.scss"],
  encapsulation: ViewEncapsulation.None,
})
export class StichingOrderDetailComponent implements OnInit {
  contentHeader: object;
  customerLedgerForm: FormGroup;
  tableDataArray = [];
  
  constructor(
    private fb: FormBuilder,
    private reportService: ReportService,
    private _router: Router,
    private _stichingCustomerOrder: StichingCustomerOrderReport
  ) {}

  makeReactiveForm() {
    this.customerLedgerForm = this.fb.group({
      fromDate: [null, [Validators.required]],
      toDate: [null, [Validators.required]],
    });
  }

  onSubmit(formValue) {
    if (this.customerLedgerForm.invalid) {
      return;
    }
    this.reportService.stichCustomerOrderReport(formValue).subscribe((res) => {
      if (res.data) {
        debugger
        this.tableDataArray = res.data;
      } else if (!res.data) {
        this.tableDataArray = [];
      }
    });
  }

  printReport() {
    const passAPi = {
      toDate: this.customerLedgerForm.value.toDate,
      fromDate: this.customerLedgerForm.value.fromDate,
      data: this.tableDataArray
    };
  
    this._stichingCustomerOrder.generatePdfContent(passAPi);
    this._router.navigate(["/reports/view"]);
  }

  getTotalAmount(): number {
    return this.tableDataArray.reduce((total, item) => total + +item.total_amount, 0);
  }

  getAmountReceived(): number {
    return this.tableDataArray.reduce((total, item) => total + +item.amount_received, 0);
  }

  getBalanceAmount(): number {
    return this.tableDataArray.reduce((total, item) => total + +item.balance_amount, 0);
  }

  ngOnInit(): void {
    this.makeReactiveForm();
    this.contentHeader = {
      headerTitle: "Reports",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: " Stiching Customer Orders ",
            isLink: true,
            link: "/",
          },
          {
            name: "Report",
            isLink: false,
          },
        ],
      },
    };
  }
}
