import { Injectable } from "@angular/core";
import { BehaviorSubject, Subject } from "rxjs";
import { reportViewInHtmlReport } from "./reportViewInHtml.service";
import {
  dateFormatter,
  thousandSeparator,
  handleNullUndefinedValues,
} from "../date-format";
@Injectable({
  providedIn: "root",
})
export class karigarLedgerReport {
  constructor(private _reportView: reportViewInHtmlReport) {}

  async generatePdfContent(getDetail) {
    getDetail = await handleNullUndefinedValues(getDetail);
    let totalCredit = 0;
    let totalDebit = 0;
    let totalBalance = 0;
    const makePdf = `<div class="card invoice-preview-card">
    <div class="divider">
    <div class="divider-text"><h3>Karigar Payment Ledger (Tailoring)</h3></div>
  </div>
      <div class="card-body invoice-padding py-1">
        <div class="d-flex justify-content-between">
          <div>
          <div class="invoice-date-wrapper mb-0">
          <p class="invoice-date">From Date: </p>
          <p class="invoice-date-title ml-25">${dateFormatter(
            getDetail.fromDate
          )}</p>
        </div>
          <div class="invoice-date-wrapper mb-0">
            <p class="invoice-date">To Date: </p>
            <p class="invoice-date-title ml-25">${dateFormatter(
              getDetail.toDate
            )}</p>
          </div>
          </div>
        </div>
      </div>
    <hr class="m-0" />
    <div class="card-body invoice-padding pt-0">
      <div class="row invoice-spacing">
        <div class="col-6 p-0">
          <table>
            <tbody>
              <tr>
                <td class="pr-1">Karigar Name:</td>
                <td>${getDetail.karigarName}</td>
              </tr>
              <tr>
                <td class="pr-1">Address:</td>
                <td>${getDetail.address}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="col-6 p-0">
          <div class="float-right">
            <table>
              <tbody>
                <tr>
                  <td class="pr-1">Karigar Number:</td>
                  <td>${getDetail.karigarNumber}</td>
                </tr>
                <tr>
                 <td class="pr-1">Designation:</td>
                 <td>${getDetail.designation}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
    <div class="table-responsive">
      <table class="table">
        <colgroup>
            <col width="15%" />
            <col width="15%" />
            <col width="40%" />
            <col width="10%" />
            <col width="10%" />
            <col width="10%" />
        </colgroup>
        <thead>
          <tr>
            <th class="py-1">Date</th>
            <th class="py-1">Payment Type</th>
            <th class="py-1">Remarks</th>
            <th class="py-1">Credit</th>
            <th class="py-1">Debit</th>
            <th class="py-1">Balance</th>
          </tr>
        </thead>
        <tbody>
          ${getDetail.data.map((element) => {
            totalCredit += +element.credit;
            totalDebit += +element.debit;
            totalBalance += +element.balance;
            return `
          <tr class="border-bottom">
            <td class="py-1">${dateFormatter(element.toDate)}</td>
            <td class="py-1">${element.paymentType}</td>
            <td class="py-1">${element.remarks}</td>
            <td class="py-1 text-right">${thousandSeparator(element.credit)}</td>
            <td class="py-1 text-right">${thousandSeparator(element.debit)}</td>
            <td class="py-1 text-right">${thousandSeparator(element.balance)}</td>
          </tr>
          `;
          })}
          <tr class="border-bottom border-top bg-light">
            <td colspan="3"><span class="font-weight-bold">Total</span></td>
            <td><span class="font-weight-bold text-right">${thousandSeparator(totalCredit)}</span></td>
            <td><span class="font-weight-bold text-right">${thousandSeparator(totalDebit)}</span></td>
            <td><span class="font-weight-bold text-right">${thousandSeparator(totalBalance)}</span></td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="card-body invoice-padding pt-0">
      <div class="row">
        <div class="col-12" style="height: 100px">
          <span class="font-weight-bold">. </span>
        </div>
      </div>
    </div>
  </div>`;
    this._reportView.getPdfContent.next(makePdf);
  }
}
