import { Injectable } from "@angular/core";
import { BehaviorSubject, Subject } from "rxjs";
import { reportViewInHtmlReport } from "./reportViewInHtml.service";
import {
  dateFormatter,
  thousandSeparator,
  handleNullUndefinedValues,
} from "../date-format";
@Injectable({
  providedIn: "root",
})
export class invoiceSummaryReport {
  constructor(private _reportView: reportViewInHtmlReport) {}

  async generatePdfContent(getDetail) {
    getDetail =await handleNullUndefinedValues(getDetail);
    let totalAmount = 0;
    let amountReceived = 0;
    let balanceAmount = 0;
    const makePdf = `<div class="card invoice-preview-card">
    <div class="divider">
    <div class="divider-text"><h3>INVOICE SUMMARY (FABRIC) </h3></div>
  </div>
      <div class="card-body invoice-padding py-1">
        <div class="d-flex justify-content-between">
          <div>
          <div class="invoice-date-wrapper mb-0">
          <p class="invoice-date">From Date: </p>
          <h5 class="invoice-date-title ml-25">${dateFormatter(
            getDetail.fromDate
          )}</h5>
        </div>
          <div class="invoice-date-wrapper mb-0">
            <p class="invoice-date">To Date: </p>
            <p class="invoice-date-title ml-25">${dateFormatter(
              getDetail.toDate
            )}</p>
          </div>
          </div>
        </div>
      </div>
    <hr class="m-0" />
    <div class="card-body invoice-padding pt-0">
      <div class="row invoice-spacing">
        <div class="col-6 p-0">
          <table>
            <tbody>
              <tr>
                <td class="pr-1">Customer Name:</td>
                <td>${getDetail.customer_name}</td>
              </tr>
              <tr>
                <td class="pr-1">Address:</td>
                <td>${getDetail.address}</td>
              </tr>
              <tr>
                <td class="pr-1">Country:</td>
                <td>${getDetail.address}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="col-6 p-0">
          <div class="float-right">
            <table>
              <tbody>
                <tr>
                  <td class="pr-1">Mobile No:</td>
                  <td>${getDetail.customer_phone}</td>
                </tr>
                <tr>
                 <td class="pr-1">Email:</td>
                 <td>${getDetail.email}</td>
                </tr>
                <tr>
                  <td class="pr-1">City:</td>
                  <td>${getDetail.city}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
    <div class="table-responsive">
      <table class="table">
        <thead>
          <tr>
            <th class="py-1">Date</th>
            <th class="py-1">Order Number</th>
            <th class="py-1">Customer Name</th>
            <th class="py-1">Order Amount</th>
            <th class="py-1">Amount Received</th>
            <th class="py-1">Balance</th>
          </tr>
        </thead>
        <tbody>
          ${getDetail.data.map((element) => {
       
            totalAmount += +element.totalNetAmount;
            amountReceived += +element.amountReceived;
            balanceAmount += +element.netBalanceAmount;
            return `
          <tr class="border-bottom">
            <td class="py-1">${dateFormatter(element.created_at)}</td>
            <td class="py-1">#NW-00${element.id}</td>
            <td class="py-1">${element.supplierName}</td>
            <td class="py-1 text-right">${thousandSeparator(element.totalNetAmount)}</td>
            <td class="py-1 text-right">${thousandSeparator(element.amountReceived)}</td>
            <td class="py-1 text-right">${thousandSeparator(element.netBalanceAmount)}</td>
          </tr>
          `;
          })}
          <tr class="border-bottom border-top bg-light">
            <td colspan="3"><span class="font-weight-bold">Total</span></span></td>
            <td><span class="font-weight-bold text-right">${thousandSeparator(totalAmount)}</span></td>
            <td><span class="font-weight-bold text-right">${thousandSeparator(amountReceived)}</span></td>
            <td><span class="font-weight-bold text-right">${thousandSeparator(balanceAmount)}</span></td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="card-body invoice-padding pt-0">
      <div class="row">
        <div class="col-12" style="height: 100px">
          <span class="font-weight-bold">. </span>
        </div>
      </div>
    </div>
  </div>`;
    this._reportView.getPdfContent.next(makePdf);
  }
}
