import { Component, ElementRef, OnInit, ViewChild } from "@angular/core";
import { orderInvoiceReport } from "app/shared/reports/customerOrderInvoice.service";
import { jsPDF } from "jspdf";
import html2canvas from "html2canvas";
import { ActivatedRoute } from "@angular/router";
import { DomSanitizer, SafeHtml } from "@angular/platform-browser";
import { Location } from "@angular/common";
import { measurementInvoiceReport } from "app/shared/reports/measurement.service";
import { customerLedgerInvoiceReport } from "app/shared/reports/customerLedger.service";
import { customerInvoiceFabricReport } from "app/shared/reports/customerInvoiceFabric.service";
import { reportViewInHtmlReport } from "app/shared/reports/reportViewInHtml.service";
@Component({
  selector: "app-view-report",
  templateUrl: "./view-report.component.html",
  styleUrls: ["./view-report.component.scss"],
})
export class ViewReportComponent implements OnInit {
  @ViewChild("PrintSection") content: ElementRef;
  innerHtmlReport: SafeHtml;
  public returnUrl: string;

  constructor(
    private _reportView: reportViewInHtmlReport,
    private activeRoute: ActivatedRoute,
    private sanitizer: DomSanitizer,
    private _location: Location
  ) {}

  ngOnInit(): void {
    this._reportView.getData().subscribe((res) => {
      this.innerHtmlReport = this.sanitizer.bypassSecurityTrustHtml(res);
    });  
  }

  backToPage() {
    this._location.back();
  }

  downloadPDF() {
    const element = this.content.nativeElement;
    html2canvas(element).then((canvas) => {
      const pdf = new jsPDF("p", "mm", "a4");
      const imgData = canvas.toDataURL("image/png");
      const imgProps = pdf.getImageProperties(imgData);
      const pdfWidth = pdf.internal.pageSize.getWidth();
      const pdfHeight = (imgProps.height * pdfWidth) / imgProps.width;
      pdf.addImage(imgData, "JPEG", 0, 0, pdfWidth, pdfHeight);
      pdf.save("content.pdf");
    });
  }
}
