import { Component, OnInit, ViewEncapsulation } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { UserService } from "app/modules/user/service/user.service";
import { ReportService } from "../../services/report.service";
import { Router } from "@angular/router";
import { customerLedgerInvoiceReport } from "app/shared/reports/customerLedger.service";

@Component({
  selector: "app-customer-ledger",
  templateUrl: "./customer-ledger.component.html",
  styleUrls: ["./customer-ledger.component.scss"],
  encapsulation: ViewEncapsulation.None,
})
export class CustomerLedgerComponent implements OnInit {
  contentHeader: object;
  customerLedgerForm: FormGroup;
  tableDataArray = [];
  userDetail;
  customerData = {
    customerName: null,
    customerNumber: null,
    email:null,
    address:null,
    city:null,
  };
  constructor(
    private _customerService: UserService,
    private fb: FormBuilder,
    private reportService: ReportService,
    private _router: Router,
    private _customerLedger: customerLedgerInvoiceReport
  ) {}

  makeReactiveForm() {
    this.customerLedgerForm = this.fb.group({
      customerName: ["", [Validators.required]],
      customerMobile: ["", [Validators.required]],
      fromDate: [],
      toDate: [],
    });
  }

  onSubmit(formValue) {
    if (this.customerLedgerForm.invalid) {
      return;
    }
    this.reportService.customerLedgerReport(formValue).subscribe((res) => {
      if (res.data) {
      //   this.balanceLedger(res.data);
        this.tableDataArray = res.data;
      } else if (!res.data) {
        this.tableDataArray = [];
      }
    });
  }

  getTotalDebit(): number {
    return this.tableDataArray.reduce((total, item) => total + item.Debit, 0);
  }

  getTotalCredit(): number {
    return this.tableDataArray.reduce((total, item) => total + item.Credit, 0);
  }

  getTotalBalance(): number {
    return this.tableDataArray.reduce((total, item) => total + item.Balance, 0);
  }
  printReport() {
    const passAPi = {
      customer_name: this.customerData.customerName,
      customer_phone: this.customerData.customerNumber,
      email: this.customerData.email,
      address: this.customerData.address,
      city: this.customerData.city,
      toDate: this.customerLedgerForm.controls.toDate.value,
      fromDate: this.customerLedgerForm.controls.fromDate.value,
      data: this.tableDataArray
    };
  
    this._customerLedger.generatePdfContent(passAPi);
    this._router.navigate(["/reports/view"]);
  }

  balanceLedger(tableData) {
    let checkBalance = tableData[tableData.length - 1].Balance;
    this.tableDataArray = tableData;
    this.tableDataArray.unshift({
      Date: null,
      OrderNumber: null,
      Narration: "BALANCE B/D",
      Debit: checkBalance < 0 ? null : checkBalance,
      Credit: checkBalance > 0 ? null : checkBalance,
      Balance: checkBalance,
    });
  }

  getUserDetails() {
    this._customerService.getAll().subscribe((res) => {
      if (res.data) {
        this.userDetail = res.data.filter(
          (type) => type.customerType == "Stitching"
        );
      }
    });
  }

  selectCustomer(selectedCustomer) {
    this.customerData.customerName = selectedCustomer.full_name;
    this.customerData.customerNumber = selectedCustomer.phone;
    this.customerData.email = selectedCustomer.email;
    this.customerData.address = selectedCustomer.address;
    this.customerData.city = selectedCustomer.city;
  }
  ngOnInit(): void {
    this.makeReactiveForm();
    this.getUserDetails();
    this.contentHeader = {
      headerTitle: "Reports",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: "Customer Ledger",
            isLink: true,
            link: "/",
          },
          {
            name: "Report",
            isLink: false,
          },
        ],
      },
    };
  }
}
