import { Component, OnInit, ViewEncapsulation } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { UserService } from "app/modules/user/service/user.service";
import { ReportService } from "../../services/report.service";
import { Router } from "@angular/router";
import { WholesaleFabricSalesReport } from "app/shared/reports/wholesaleSales.service";

@Component({
  selector: "app-wholesale-sale",
  templateUrl: "./wholesale-sale.component.html",
  styleUrls: ["./wholesale-sale.component.scss"],
  encapsulation: ViewEncapsulation.None,
})
export class WholesaleSalesComponent implements OnInit {
  contentHeader: object;
  customerLedgerForm: FormGroup;
  tableDataArray = [];
  
  constructor(
    private fb: FormBuilder,
    private reportService: ReportService,
    private _router: Router,
    private _expensesSummary: WholesaleFabricSalesReport
  ) {}

  makeReactiveForm() {
    this.customerLedgerForm = this.fb.group({
      fromDate: [null, [Validators.required]],
      toDate: [null, [Validators.required]],
    });
  }

  onSubmit(formValue) {
    if (this.customerLedgerForm.invalid) {
      return;
    }
    this.reportService.wholeSaleSaleReport(formValue).subscribe((res) => {
      if (res.data) {
        debugger
        this.tableDataArray = res.data;
      } else if (!res.data) {
        this.tableDataArray = [];
      }
    });
  }

  printReport() {
    const passAPi = {
      toDate: this.customerLedgerForm.value.toDate,
      fromDate: this.customerLedgerForm.value.fromDate,
      data: this.tableDataArray
    };
  
    this._expensesSummary.generatePdfContent(passAPi);
    this._router.navigate(["/reports/view"]);
  }

  getItemQuantity(): number {
    return this.tableDataArray.reduce((total, item) => total + +item.itemQuantity, 0);
  }

  getItemPrice(): number {
    return this.tableDataArray.reduce((total, item) => total + +item.itemPrice, 0);
  }

  getItemAmount(): number {
    return this.tableDataArray.reduce((total, item) => total + +item.itemAmount, 0);
  }

  getItemDelivery(): number {
    return this.tableDataArray.reduce((total, item) => total + +item.itemDelivery, 0);
  }

  getItemNetAmount(): number {
    return this.tableDataArray.reduce((total, item) => total + +item.itemNetAmount, 0);
  }

  ngOnInit(): void {
    this.makeReactiveForm();
    this.contentHeader = {
      headerTitle: "Reports",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: "Wholesale Fabric Sales",
            isLink: true,
            link: "/",
          },
          {
            name: "Report",
            isLink: false,
          },
        ],
      },
    };
  }
}
