import { Component, OnInit } from '@angular/core';
import { ReportService } from '../../services/report.service';
import { Router } from '@angular/router';
import { inventoryFabricReport } from 'app/shared/reports/inventoryReport.service';

@Component({
  selector: 'app-list-report',
  templateUrl: './list-report.component.html',
  styleUrls: ['./list-report.component.scss']
})
export class ListReportComponent implements OnInit {
  contentHeader:object
  reportsDetail = [
    {
      url:'',
      icon:'',
      text:''
    }
  ]
  constructor(
    private reportService: ReportService,
    private _router: Router,
    private inventory:inventoryFabricReport
  ) { }

  
  makeReport(){
    this.reportService.inventoryReport().subscribe((getDetail) => {
      if(getDetail.data){
        this.inventory.generatePdfContent(getDetail.data)
          this._router.navigate(['/reports/view']);        
      }
    });
}

  ngOnInit(): void {
    this.contentHeader = {
      headerTitle: "Reports",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: "Report List",
            isLink: true,
            link: "/",
          },
          {
            name: "",
            isLink: false,
          },
        ],
      },
    };
  }

}
