import { Injectable } from "@angular/core";
import { BehaviorSubject, Subject } from "rxjs";
import { reportViewInHtmlReport } from "./reportViewInHtml.service";
import {
  dateFormatter,
  thousandSeparator,
  handleNullUndefinedValues,
} from "../date-format";
@Injectable({
  providedIn: "root",
})
export class expensesDetailsReport {
  constructor(private _reportView: reportViewInHtmlReport) {}

  async generatePdfContent(getDetail) {
    getDetail = await handleNullUndefinedValues(getDetail);
    let totalAmount = 0;
    const makePdf = `
    <div class="card invoice-preview-card">
    <div class="divider">
      <div class="divider-text"><h3>EXPENSES DETAIL</h3></div>
    </div>
    <div class="d-flex justify-content-between">
      <div class="ml-2">
        <div class="invoice-date-wrapper mb-0">
          <p class="invoice-date">From Date:</p>
          <p class="invoice-date-title ml-25">${dateFormatter(
            getDetail.fromDate
          )}</p>
        </div>
        <div class="invoice-date-wrapper">
          <p class="invoice-date">To Date:</p>
          <p class="invoice-date-title ml-25">${dateFormatter(
            getDetail.toDate
          )}</p>
        </div>
      </div>
    </div>
    <hr class="m-0" />
    <div class="card-body invoice-padding pt-0">
      <div class="row invoice-spacing">
        <div class="col-6 p-0">
          <table>
            <tbody>
              <tr>
                <td class="pr-1"><strong>Business Type:</strong></td>
                <td>${getDetail.businessType}</td>
              </tr>
              <tr>
                <td class="pr-1"><strong>Expense Type:</strong></td>
                <td>${getDetail.expensesType}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <div class="table-responsive">
      <table class="table">
        <colgroup>
          <col width="20%" />
          <col width="60%" />
          <col width="20%" />
        </colgroup>
        <thead>
          <tr>
            <th class="py-1">Date</th>
            <th class="py-1">Remarks</th>
            <th class="py-1">Amount</th>
          </tr>
        </thead>
        <tbody>
          ${getDetail.data.map((element) => {
            totalAmount += +element.amount;
            return `<tr class="border-bottom">
                      <td class="py-1">${dateFormatter(element.created_at)}</td>
                      <td class="py-1">${element.remarks}</td>
                      <td class="py-1 text-right">${thousandSeparator(element.amount)}</td>
                    </tr>
                    `;
          })}
          <tr class="border-bottom border-top bg-light">
            <td colspan="2">
              <span class="font-weight-bold">Total</span>
            </td>
            <td class="text-right"><span class="font-weight-bold ">${thousandSeparator(
              totalAmount
            )}</span></td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="card-body invoice-padding pt-0">
      <div class="row">
        <div class="col-12" style="height: 100px">
          <span class="font-weight-bold">. </span>
        </div>
      </div>
    </div>
  </div>`;
    this._reportView.getPdfContent.next(makePdf);
  }
}
