import { Injectable } from "@angular/core";
import { BehaviorSubject, Subject } from "rxjs";
import { reportViewInHtmlReport } from "./reportViewInHtml.service";
import {
  dateFormatter,
  thousandSeparator,
  handleNullUndefinedValues,
} from "../date-format";
@Injectable({
  providedIn: "root",
})
export class customerLedgerInvoiceReport {
  constructor(private _reportView: reportViewInHtmlReport) {}

  async generatePdfContent(getDetail) {
    getDetail = await handleNullUndefinedValues(getDetail);
    let totalCredit = 0;
    let totalDebit = 0;
    let totalBalance = 0;
    const makePdf = `
    <div class="card invoice-preview-card">
  <div class="divider">
  <div class="divider-text"><h3>CUSTOMER LEDGER (TAILORING)</h3></div>
</div>
    <div class="card-body invoice-padding py-1">
      <div class="d-flex justify-content-between">
        <div>
          <div class="invoice-date-wrapper mb-0">
              <p class="invoice-date">From Date:</p>
              <p class="invoice-date-title ml-25">${dateFormatter(
                getDetail.fromDate
              )}</p>
          </div>
          <div class="invoice-date-wrapper">
            <p class="invoice-date">To Date:</p>
            <p class="invoice-date-title ml-25">${dateFormatter(
              getDetail.toDate
            )}</p>
          </div>
        </div>
      </div>
    </div>
    <hr class="m-0" />
    <div class="card-body invoice-padding pt-0">
      <div class="row invoice-spacing">
        <div class="col-6 p-0">
          <table>
            <tbody>
              <tr>
                <td class="pr-1">Customer Name:</td>
                <td>${getDetail.customer_name}</td>
              </tr>
              <tr>
                <td class="pr-1">Person Name:</td>
                <td>---</td>
              </tr>
              <tr>
                <td class="pr-1">Address:</td>
                <td>${getDetail.address}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="col-6 p-0">
          <div class="float-right">
            <table>
              <tbody>
                <tr>
                  <td class="pr-1">Mobile No:</td>
                  <td>${getDetail.customer_phone}</td>
                </tr>
                <tr>
                  <td class="pr-1">City:</td>
                  <td>${getDetail.city}</td>
                </tr>
                <tr>
                  <td class="pr-1">Email:</td>
                  <td>${getDetail.email}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
    <div class="table-responsive">
      <table class="table">
        <colgroup>
          <col width="15%" />
          <col width="15%" />
          <col width="40%" />
          <col width="10%" />
          <col width="10%" />
          <col width="10%" />
        </colgroup>
        <thead>
          <tr>
            <th class="py-1">Date</th>
            <th class="py-1">Order Number</th>
            <th class="py-1">Narration</th>
            <th class="py-1">Debit</th>
            <th class="py-1">Credit</th>
            <th class="py-1">Balance</th>
          </tr>
        </thead>
        <tbody>
          ${getDetail.data.map((element) => {
            totalCredit += +element.Credit;
            totalDebit += +element.Debit;
            totalBalance += +element.Balance;
            return `
          <tr class="border-bottom">
            <td class="py-1">${dateFormatter(element.Date)}</td>
            <td class="py-1">${element.OrderNumber}</td>
            <td class="py-1">${element.Narration}</td>
            <td class="py-1 text-right">${thousandSeparator(element.Debit)}</td>
            <td class="py-1 text-right">${thousandSeparator(element.Credit)}</td>
            <td class="py-1 text-right">${thousandSeparator(element.Balance)}</td>
          </tr>
          `;
          })}
          <tr class="border-bottom border-top bg-light">
          <td colspan="3">Total</td>
          <td class="text-right"><span class="font-weight-bold">${thousandSeparator(
            totalDebit
          )}</span></td>
          <td class="text-right"><span class="font-weight-bold">${thousandSeparator(
            totalCredit
          )}</span></td>
          <td class="text-right"><span class="font-weight-bold">${thousandSeparator(
            totalBalance
          )}</span></td>
        </tr>
        </tbody>
      </table>
    </div>
    <div class="card-body invoice-padding pt-0">
      <div class="row">
        <div class="col-12" style="height: 100px">
          <span class="font-weight-bold">. </span>
        </div>
      </div>
    </div>
  </div>`;
    this._reportView.getPdfContent.next(makePdf);
  }
}
