import { Component, OnInit, ViewEncapsulation } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ReportService } from "../../services/report.service";
import { Router } from "@angular/router";
import { orderSummaryReport } from "app/shared/reports/orderSummaryTailoring.service";
import { ItemMasterService } from "app/modules/item-master/service/itemMaster.service";
import { inventoryActivityReport } from "app/shared/reports/inventoryActivity.service";

@Component({
  selector: "app-inventory-activity",
  templateUrl: "./inventory-activity.component.html",
  styleUrls: ["./inventory-activity.component.scss"],
  encapsulation: ViewEncapsulation.None,
})
export class InventoryActivityComponent implements OnInit {
  contentHeader: object;
  customerLedgerForm: FormGroup;
  tableDataArray = [];
  userDetail;
  itemMaster = {
    itemName: null,
    itemUOM: null,
    article: null,
    color: null,
  };
  constructor(
    private _itemMasterService: ItemMasterService,
    private fb: FormBuilder,
    private reportService: ReportService,
    private _router: Router,
    private _inventoryActivity: inventoryActivityReport
  ) {}

  makeReactiveForm() {
    this.customerLedgerForm = this.fb.group({
      itemName: ["", [Validators.required]],
      itemUOM: ["", [Validators.required]],
      fromDate: [],
      toDate: [],
    });
  }

  onSubmit(formValue) {
    if (this.customerLedgerForm.invalid) {
      return;
    }
    this.reportService.inventoryActivityReport(formValue).subscribe((res) => {
      if (res.data) {
        this.tableDataArray = res.data;
      } else if (!res.data) {
        this.tableDataArray = [];
      }
    });
  }

  printReport() {
    const passAPi = {
      itemName: this.itemMaster.itemName,
      itemUOM: this.itemMaster.itemUOM,
      article: this.itemMaster.article,
      color: this.itemMaster.color,
      toDate: this.customerLedgerForm.controls.toDate.value,
      fromDate: this.customerLedgerForm.controls.fromDate.value,
      data: this.tableDataArray,
    };

    this._inventoryActivity.generatePdfContent(passAPi);
    this._router.navigate(["/reports/view"]);
  }

  getUserDetails() {
    this._itemMasterService.getAll().subscribe((res) => {
      if (res.data) {
        
        this.userDetail = res.data
      }
    });
  }


  
  getTotalStockIn(): number {
    return this.tableDataArray.reduce((total, item) => total + +item.StockIn, 0);
  }
  
  getTotalStockOut(): number {
    return this.tableDataArray.reduce((total, item) => total + +item.StockOut, 0);
  }
  
  selectCustomer(selectedCustomer) {
    this.itemMaster.itemName = selectedCustomer.name;
    this.itemMaster.itemUOM = selectedCustomer.UOM;
    this.itemMaster.article = selectedCustomer.article;
    this.itemMaster.color = selectedCustomer.color;
  }
  ngOnInit(): void {
    this.makeReactiveForm();
    this.getUserDetails();
    this.contentHeader = {
      headerTitle: "Reports",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: "Inventory Activity",
            isLink: true,
            link: "/",
          },
          {
            name: "Report",
            isLink: false,
          },
        ],
      },
    };
  }
}
